<template>
  <section class="portfolio-fixed">
    <div class="container-fluid rest">
      <div class="row">
        <div class="col-lg-6 rest" style="position: relative">
          <div class="left" id="sticky_item">
            <div v-for="(item, index) in data" :id="`tab-${index + 1}`" class="img bg-img"
              :data-background="`${lightMode ? '/light/' : '/dark/'}${item.image}`" :key="index" />
          </div>
        </div>
        <div class="col-lg-6 sub-bg right">
          <div :class="`cont ${index === 0 ? 'active' : ''}`" :data-tab="`tab-${index + 1}`" v-for="(item, index) in data"
            :key="index">
            <div class="img-hiden">
              <img :src="`${lightMode ? '/light/' : '/dark/'}${item.image}`" alt="" />
            </div>
            <span class="sub-title mb-15">{{ item.number }} {{ item.type }}</span>
            <h2 class="mb-15">{{ item.title }}.</h2>
            <div class="row">
              <div class="col-md-9">
                <p>{{ item.text }}.</p>
                <div class="vew-all mt-50 ml-25">
                  <!-- <NuxtLink to="/dark/project-details2" class="sub-title">
                    瞭解更多
                    <span>
                      <svg width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path
                          d="M13.922 4.5V11.8125C13.922 11.9244 13.8776 12.0317 13.7985 12.1108C13.7193 12.1899 13.612 12.2344 13.5002 12.2344C13.3883 12.2344 13.281 12.1899 13.2018 12.1108C13.1227 12.0317 13.0783 11.9244 13.0783 11.8125V5.51953L4.79547 13.7953C4.71715 13.8736 4.61092 13.9176 4.50015 13.9176C4.38939 13.9176 4.28316 13.8736 4.20484 13.7953C4.12652 13.717 4.08252 13.6108 4.08252 13.5C4.08252 13.3892 4.12652 13.283 4.20484 13.2047L12.4806 4.92188H6.18765C6.07577 4.92188 5.96846 4.87743 5.88934 4.79831C5.81023 4.71919 5.76578 4.61189 5.76578 4.5C5.76578 4.38811 5.81023 4.28081 5.88934 4.20169C5.96846 4.12257 6.07577 4.07813 6.18765 4.07812H13.5002C13.612 4.07813 13.7193 4.12257 13.7985 4.20169C13.8776 4.28081 13.922 4.38811 13.922 4.5Z"
                          fill="currentColor"></path>
                      </svg>
                    </span>
                  </NuxtLink> -->
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script setup>
import { onMounted, onUnmounted } from 'vue';
//= Scripts
import loadBackgroudImages from '@/common/loadBackgroudImages';
import isInView from '@/common/isInView';
//= Data
import data from '@/data/OnePage/portfolio.json';

const { lightMode } = defineProps(['lightMode']);

function handleShowTabs() {
  isInView({
    selector: '.portfolio-fixed .sub-bg .cont',
    isElements: true,
    callback(element) {
      element.classList.add('current');
      document.querySelector("#" + element.getAttribute('data-tab')).classList.add('current');
    },
    whenOutOfView(element) {
      element.classList.remove('current');
      document.querySelector("#" + element.getAttribute('data-tab')).classList.remove('current');
    }
  });

  const leftSide = document.getElementById('sticky_item');
  if (!leftSide) return;
  const width = leftSide.getBoundingClientRect().width;
  const portfolio = document.querySelector('.portfolio-fixed').getBoundingClientRect();

  if (portfolio.top < 75 && portfolio.height / 2 < portfolio.bottom) {
    leftSide.style.position = 'fixed';
    leftSide.style.top = '0px';
    leftSide.style.width = width + 'px';
    leftSide.classList.remove('is_stuck')
  } else if (portfolio.height / 2 > portfolio.bottom) {
    leftSide.style.position = 'absolute';
    leftSide.style.top = 'auto';
    leftSide.style.bottom = '0';
    leftSide.style.width = width + 'px';
    leftSide.classList.add('is_stuck')
    document.querySelector(`#tab-${document.querySelectorAll("[data-tab]").length}`).classList.add('current');
  } else {
    leftSide.style.position = 'relative';
    leftSide.style.top = 'unset';
    leftSide.style.bottom = 'unset';
    leftSide.style.width = 'auto';
  }
}

onMounted(() => {
  loadBackgroudImages();
  window.addEventListener('scroll', handleShowTabs);
});

onUnmounted(() => {
  window.removeEventListener('scroll', handleShowTabs);
});
</script>